.js body {
    .shapeTransit1,
    .shapeTransit2 {
        height: 100%;
        width: 100%;
        bottom: -100%;
        left: 0;
        position: fixed; }
    .shapeTransit1 {
        background: #d8dde6;
        z-index: 20000;
        @include pseudo;
        &::after {
            // background-image: url(../img/logo_gray.png);
            // background-size: cover;
            width: 290px;
            height: 50px;
            position: absolute;
            left: 50%;
            top: 40%;
            display: block;
            transform: translate(-50%, -50%); } }
    .shapeTransit2 {
        background: #f5f0ec;
        z-index: 21000; }


    > .loader {
        position: fixed;
        left: 0;
        right: 0;
        transition: height .4s .4s cubic-bezier(.4,0,.2,1);
        width: 100%;
        //height: 100%
        height: 100vh;
        background: #d8dde6;
        overflow: hidden;
        z-index: 999;
        .loader__wrap {
            position: absolute;
            left: 50%;
            top: 50%;
            display: block;
            transform: translate(-50%, -50%);
            width: 260px;
            height: 230px;
            @include pseudo;
            &::before {}
            &::after {}

            // &::before
            //     position: relative
            //     display: block
            //     width: 260px;
            //     height: 230px;
            //     background-image: url(../img/logo_butterfly.png);
            //     background-size: cover;
            //     transform-origin: 50% 50%;
            //     animation: butterfly 0.4s alternate infinite
            //     @keyframes butterfly
            //       to
            //         transform: scaleX(0.8);
            //         opacity: 0.5;
            &::after {
                background-image: url(../img/logo_gray.png);
                background-size: cover;
                width: 290px;
                height: 50px;
                animation: pulse 0.9s alternate infinite;
                @include sm {
                    width: 250px;
                    height: 40px; }
                @keyframes pulse {
                  from {
                    transform: scale(1.6); }
                  to {
                    transform: scale(0.8);
                    opacity: 0.5; } } } } } }
.js body {
    overflow: hidden;
    &.is-loaded {
        overflow: initial;
        > .loader {
            height: 0; } } }


.page__title .page__title-text {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        transform: scale3d(0,1,1);
        z-index: 100;
        //.is-loaded &,
        .is-animate & {
            animation-name: textDiscover;
            animation-duration: 1s;
            animation-timing-function: cubic-bezier(.645,.045,.355,1); } } }

main {
    position: relative;
    &:before {
        content: '';
        position: absolute;
        top: 60px;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: #fff;
        transform: scale3d(0,1,1);
        z-index: 100;
        //.is-loaded &,
        .is-animate & {
            animation-name: blockDiscover;
            animation-duration: 1.2s;
            animation-timing-function: cubic-bezier(.645,.045,.355,1); } } }



