header {
    font-size: 16px;
    color: #949494;
    .logo {
        float: left;
        display: block;
        width: 225px;
        height: 60px;
        margin-top: 10px;
        //margin: 2em 0;

        margin-right: 3em;
        @include sm {
            width: 80px;
            margin: 0; }
        @include pseudo;
        &:before {
            width: 100%;
            height: 50px;
            background: url('../img/logo.svg') no-repeat 50% 50%;
            background-size: contain;
            display: block;
            @include sm {
                display: none; } }
        &:after {
            width: 78px;
            height: 50px;
            background: #333333 url('../img/logo_bf_white.png') no-repeat 50% 50%;
            background-size: 50%;
            display: none;
            @include sm {
                display: block; } }
        span,
        .pic {
            text-indent: -1984px;
            display: block; }
        small,
        .title {
            display: block;
            text-align: center;
            font-size: 13px;
            height: 15px;
            line-height: 15px;
            margin-top: 22px;
            color: #ababab;
            letter-spacing: .05em;
            @include sm {
                display: none; } } }
    .breadcrumbs {
        position: absolute;
        position: absolute;
        top: 45px;
        z-index: 99;
        left: 295px;
        a {
            color: #ABABAB; }
        @include md {
            left: initial;
            right: 5%; }
        @include sm {
            display: none;
            //display: block;
            left: 0;
            right: 0;
            top: 50px;
            padding: .5em 1em;
            background: #f5f5f5;
            box-shadow: 0 10px 20px rgba(0,0,0,.25);
            a {
                color: #333; } } }



    .location {
        float: left;
        margin-right: 1em;
        margin-top: 1em;
        position: relative;
        &--change {
            border-bottom: 1px dashed #bbb;
            position: relative;
            margin-left: 20px;
            display: block;
            float: right;
            .fa {
                position: absolute;
                left: -13px;
                top: 2px; } }
        @include md {
            display: none; }
        &__name {
            width: 150px;
            height: 20px;
            overflow: hidden;
            display: block;
            white-space: nowrap;
            @media only screen and (min-width: 1025px) {
                width: initial; }
            @include md {
 }                //width: 140px;
            @include pseudo;
            &::before {
                top: 0;
                background: #fff;
                width: 1px;
                height: 100%;
                left: initial;
                right: 0;
                box-shadow: 4px 0 15px 15px #ffffff;
                @media only screen and (min-width: 1025px) {
                    display: none; } } }

        .location__choose {
            background: rgba(51, 51, 51, 0.8);
            //backdrop-filter: blur(15px);
            border-radius: 2px;
            color: #fff;
            padding: 1em 1.5em;
            width: 670px; }
        .location__choose {
            position: absolute;
            top: 30px;
            left: 0;
            transform: translate(0, 20px);
            padding-top: 14px;
            opacity: 0;
            pointer-events: none;
            transition: opacity .3s cubic-bezier(.01,.32,.11,.99),transform .3s cubic-bezier(.01,.32,.11,.99);
            z-index: 1000;

            &-title {
                font-size: 24px;
                font-weight: normal;
                margin-bottom: .5em; }
            &-location {
                @include clr;
                margin-bottom: 1em; }

            &-location-item {
                float: left;
                width: 24%;
                font-size: 16px;
                line-height: 14px;
                margin-bottom: .5em;
                a {
                    color: #FFFFFF; } }
            .input {
                margin-bottom: 1em; }
            .input input {
                font-size: 14px;
                padding: 0 10px;
                width: 40%;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0; }
            .btn {
                font-size: 14px;
                text-align: center;
                padding: 0px 30px;
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
                line-height: 44px;
                height: 44px; }

            &-close {
                position: absolute;
                right: 16px;
                top: 16px;
                width: 16px;
                height: 16px;
                opacity: 0.5;
                text-indent: -1984px;
                cursor: pointer;

                &:hover {
                    opacity: 1; }
                &:before, &:after {
                    position: absolute;
                    left: 8px;
                    content: ' ';
                    height: 18px;
                    width: 4px;
                    background-color: #FFF; }
                &:before {
                    transform: rotate(45deg); }
                &:after {
                    transform: rotate(-45deg); } }
            .isLocationSwitch & {
                pointer-events: auto;
                opacity: 1;
                transform: none; } } }
    .header__right {
        float: right; }
    .userArea, .basket {
        margin-top: 1.15em; }
    .userArea {
        float: left;
        //margin-right: 1em;
        position: relative;
        display: flex;
        @include sm {
            margin-top: 1.1em; }
        > * {
            margin-right: 25px; }
        > a {
            @include sm {
                margin-right: 45px;
                @include pseudo;
                &::after {
                    background: #EDEDED;
                    width: 1;
                    height: 200px;
                    top: -100px;
                    right: -30px;
                    left: initial; } } }
        &__like {
            position: relative;
            .icon-like {
                fill: #FF4062;
                //display: none
                @include sm {
                    display: block; } }
            &-title {
                @include sm {
                    display: none; } }
            &--count {
                position: absolute;
                right: -15px;
                top: -2px;

                border-radius: 50%;
                background: #FF4062;
                color: #fff;
                font-size: 10px;
                width: 20px;
                line-height: 20px;
                height: 20px;
                text-align: center; } }

        &__sign {
            .icon-sign {
                fill: #000;
                display: none;
                @include sm {
                    display: block; } }
            &-title {
                @include sm {
                    display: none; } } } }
    .basket {
        float: left;
        margin-right: 2em;
        @include sm {
            margin-top: 1em; }
        &--order {
            background: url('../img/basket.svg') no-repeat right 0;
            position: relative;
            display: block;
            padding-right: 25px;
            height: 25px; }
        &-title {
            display: block;
            @include sm {
                display: none; } }
        &--count {
            position: absolute;
            right: -10px;
            top: 0;

            border-radius: 50%;
            background: #000;
            color: #fff;
            font-size: 10px;
            width: 20px;
            line-height: 20px;
            height: 20px;
            text-align: center; } } }


.b-login-form {
    background: rgba(255, 255, 255, 0.9);
    //backdrop-filter: blur(15px);
    border: 1px solid #BDBDBD;
    box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    padding: 1em 1.5em;
    width: 390px; }

.b-login-form {
    position: absolute;
    top: 30px;
    left: -200px;
    transform: translate(0, 20px);
    padding-top: 14px;
    opacity: 0;
    pointer-events: none;
    transition: opacity .4s,transform .3s cubic-bezier(.01,.32,.11,.99);
    z-index: 900;
    @include sm {
        width: 300px;
        left: -170px; }
    &.isVisible {
        pointer-events: auto;
        opacity: 1;
        transform: none; } }
.b-login-form {
    &-wrap {
        position: relative; }
    &__title {
        font-weight: normal;
        font-size: 24px;
        color: #333333;
        margin-bottom: 10px; }
    &--register {
        color: #949494;
        font-size: 14px;
        border-bottom: 1px dashed #bbb;
        position: absolute;
        right: 0;
        top: 5px; }
    .input {
        margin-bottom: 10px;
        label {
            padding-bottom: 10px;
            font-size: 14px; }
        input {
            padding: 10px;
            font-size: 14px;
            height: 42px; } }
    &-btn {
        padding: 0 25px;
        .lostpass {
            color: #949494;
            font-size: 14px;
            line-height: 25px;
            text-decoration: underline; }
        .btn {
            float: right; } } }



