.js-toggle-nav {
    float: left;
    display: block;
    margin-top: 14px;
    font-size: 7px;
    background: url('../img/nav_open.svg') no-repeat 0 0;
    width: 25px;
    height: 25px;
    display: none;
    &.is-active {
        background: url('../img/nav_close.svg') no-repeat 0 0; }
    @include sm {
        display: block; } }
