aside {
    .menu {
        color: #333;
        font-size: 18px;
        margin-bottom: 50px;
        @media screen and (max-height: 700px) {
            margin-bottom: 20px; }
        &__item {
            margin-bottom: 5px;
            position: relative;
            &.subMenu > a {
                @include pseudo;
                &:after {}
                @include menuArrow; }
            &.subMenu li {
                margin-bottom: 15px; } }
        &__item .submenu {
            margin-top: .5em;
            margin-left: 1em;
            margin-right: 2em;
            //display: none
            li {
                margin-bottom: 8px;
                font-size: 16px;
                line-height: 16px; }
            a {
                color: #949494;
                &:hover {
                    color: #000; } } }
        @media screen and (max-height: 700px) {
            &__item .submenu {
                margin-top: .25em;
                li {
                    &:last-child {
                        margin-bottom: 0; } } } } }
    //Animation
    .menu {
        &__item a {
            position: relative; }
        &__item a::before {
            content: '';
            position: absolute;
            z-index: -1;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 50%;
            opacity: 0.3;
            transform: scale3d(0,1,1);
            transform-origin: 0% 50%;
            transition: transform 0.5s;
            transition-timing-function: cubic-bezier(0.2,1,0.3,1);

            background: #6265fe;
            box-sizing: border-box;

            &:hover::before, &:focus::before, &:hover::before, &:focus::before {
                transform: scale3d(1,1,1); } } }

    transform: scale3d(1, 1, 1);
    .phone {
        float: left;
        margin-right: 2em;
        width: 260px;
        .tel {
            font-size: 24px;
            line-height: 30px; }
        &__desc {
            color: #949494;
            font-size: 15px; } }
    .smenu {
        color: #949494;
        font-size: 18px;
        margin-bottom: 50px;
        .menu__item {
            position: relative;
            margin-bottom: 5px;
            padding: 0;

            &.subMenu > a {
                @include pseudo;
                &:after {
                    @include menuArrow;
                    border-color: #949494; } }
            &.subMenu li {
                margin-bottom: 15px; } }
        a {
            color: #949494;
            transition: .25s ease;
            &:hover {
                color: #666; } }
        li ul {
            display: none; } } }
