body {
    font-family: Gals, Arial, sans-serif;
    //font-size: 18px;
    @include fs(16);
    //+f(Gals);
    //+css-lock(16, 50, 600, 1000);
    line-height: 1.4;
    //background: #F2F2F2;
    a {
        color: #333;
        text-decoration: none;
        transition: .25s ease;
        &:hover {
            color: #000; }
        &.link_modal {
            border-bottom: 1px dashed #aaa; } }
    @include pseudo;
    &:before {
        display: none; }
    &:after {
        background: #F2F2F2;
        width: 100%;
        height: 100%;
        z-index: -1;
        opacity: 0; } }
h1, .h1 {
    font-size: 36px;
    font-weight: normal;
    line-height: 32px;
    margin-bottom: 20px;
    .indexPage & {
        font-size: 48px;
        line-height: 36px;
        margin-bottom: 0;
        margin-bottom: 10px; } }

.table {
    width: 100%;
    max-width: 100%;
    caption {
        padding-top: 8px;
        padding-bottom: 8px;
        color: #777;
        text-align: left; } }

.table {
    width: 100%;
    max-width: 100%;
    margin-bottom: 20px;
    > {
        thead > tr > th,
        tbody > tr > th,
        tfoot > tr > th,
        thead > tr > td,
        tbody > tr > td,
        tfoot > tr > td {
            padding: 8px;
            line-height: 1.42857143;
            vertical-align: top;
            border-top: 1px solid #ddd; }
        thead > tr > th {
            vertical-align: bottom;
            border-bottom: 2px solid #ddd;
            font-size: 14px;
            color: #ABABAB; }
        caption + thead > tr:first-child > th, colgroup + thead > tr:first-child > th, thead:first-child > tr:first-child > th, caption + thead > tr:first-child > td, colgroup + thead > tr:first-child > td, thead:first-child > tr:first-child > td {
            border-top: 0; }
        tbody + tbody {
            border-top: 2px solid #ddd; }
        th {
            text-align: left; } } }


main {
    h2 {
        font-size: 24px;
        line-height: 22px;
        color: #333333;
        margin-top: 10px;
        margin-bottom: 10px; }
    h3 {
        font-size: 16px;
        color: #333333;
        margin-top: 10px;
        margin-bottom: 10px; }
    ul {
        margin-bottom: 10px;
        li {
            color: #949494;
            font-size: 16px;
            margin-bottom: 10px;
            margin-left: 15px;
            list-style: disc; } } }

figure.img-full {
    margin-bottom: 1em;
    img {
        position: relative;
        display: block;
        min-height: 100%;
        max-width: 100%;
        margin: 0 auto; }
    &:hover {
        img {
            transform: scale3d(1,1,1);
            transition: transform .35s; } } }




p {
    //+fs(11);
    color: #949494;
    font-size: 18px;
    margin-bottom: 20px;
    //font-size: 18px;
    .itemPage & {
 }        //font-size: 14px;
    @include sm {
        font-size: 16px; } }
p+p {
    margin-bottom: 10px; }

hr {
    border: 1px solid #eee;
    margin: 50px 0; }


//beadcrumbs
.breadcrumbs {
    @include clr;
    margin: 0 0 1em 0;
    li {
        float: left;
        font-size: 16px;
        //text-transform: uppercase;
        padding-right: 15px;
        position: relative;
        @include pseudo;
        &:before {
            display: none; }
        &:after {
            right: 5px;
            left: initial;
            content: '/'; } } }
.breadcrumbs__mobile .breadcrumbs {
    padding: 65px 15px 10px 15px;
    margin-bottom: 0;
    background: #EDEDED;
    display: none;
    @include sm {
        display: block; }
    li, a {
        color: #ABABAB; } }
//pagination
.pagination {
    @include clr;
    margin-bottom: 1em;
    li {
        float: left;
        list-style: none;
        margin-left: 0;
        margin-bottom: .75em;
        a {
            border-radius: 5px;
            border: 1px solid #BDBDBD;
            color: #949494;
            line-height: 24px;
            font-size: 14px;
            padding: 6px 12px;
            margin-right: 5px; } }
    li.active {
        a {
            color: #FFFFFF;
            background: #B09C6D;
            border: 1px solid #B09C6D; } } }



.newsletter .input-wrapper {
    position: relative;
    max-width: 340px; }

.newsletter [type=email] {
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 5px;
    padding-left: 20px;
    font-size: 14px;
    box-sizing: border-box; }
.newsletter {
    position: relative; }

.newsletter .input-wrapper {
    //position: relative;
    position: absolute;
    left: 370px;
    top: -80px;
    @include md {
        position: relative;
        left: initial;
        top: initial; }
    input {
        border: 1px solid #BDBDBD; }
    .btn {
        height: 50px;
        position: absolute;
        right: 0;
        top: 0;
        width: 50px;
        background: #B09C6D;
        color: #fff;
        font-size: 18px;
        border-radius: 5px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        padding: 0; }
    .checkbox {
        color: #949494;
        font-size: 16px;
        margin-top: 10px; }
    .checkbox input+span {
        position: relative;
        padding-left: 25px;
        display: inline-block; } }

