main {
    margin-bottom: 1em;
    padding-bottom: 1em; }
.page__hero {
    @include clr;
    margin-bottom: 1em; }


.page__title {
    @include clr;
    margin-bottom: .5em;
    margin-bottom: 25px;
    @include sm {
        margin-bottom: 15px; }
    //margin-left: -13px;
    h1 {
        margin-bottom: 0; }
    &-text {
        float: left;
        line-height: 38px;
        @include md {
            float: left; }
        @include sm {
            float: none;
            font-size: 24px; } }
    &-right {
        float: right;
        @include md {
            float: left;
            margin-left: 1em; }
        @include sm {
            margin-left: 0;
            float: none; } } }

.page__title {
    .title__viewAll {
        display: inline-block;
        opacity: .5;
        font-size: 40%;
        margin-left: 10px;
        @include sm {
            display: block;
            margin: 0; } }
    &-right {}

    &-menu {
        float: right;
        font-size: 16px;
        margin-top: 1em;
        margin-bottom: 0;
        @include sm {
            float: none; }
        li {
            float: left;
            margin-right: 1em;
            margin-bottom: 0;
            list-style: none;
            @include sm {
                margin-left: 0; }
            a {
                color: #ABABAB; } }
        .active {
            a {
                color: #333333;
                &:after {
                    border-color: #333333; } } } } }



.page__news {
    @include clr;
    margin-top: .25em;
    margin-bottom: 3em;
    .news {
        &__item {
            float: left;
            width: 48%;
            h5 {
                font-size: 18px;
                font-weight: normal;
                color: #333333;
                margin-bottom: .5em; }
            p {
                color: #ABABAB;
                font-size: 16px!important;
                margin-bottom: 5px; }

            a, .more {
                color: #333333;
                font-size: 16px!important; } } } }




.main {
    &__row {
        @include clr;
        display: flex;
        //justify-content: space-between;
        align-items: stretch; }
    &__col1 {
        width: 270px;
        @include md {
            width: 400px; }
        @include sm {
            display: none; } }
    &__col2 {
        width: 850px;
        @include md {
            width: 450px; }
        @include sm {
            width: 100%;
            padding: 0 1em; } } }
