// Состояние заказов - Подробнее
.order-full {
    &__title {
        margin-bottom: 1em;
        display: flex;
        font-size: 16px;
        color: #333333;
        align-items: center;
        @include sm {
            flex-direction: column;
            align-items: baseline; }
        * {
            flex-grow: 1; }
        .btn {
            padding: 6px 12px;
            margin: 0;
            font-size: 14px;
            line-height: initial;
            height: initial;
            width: 150px;
            @include sm {
                margin: .5em 0; } } }
    &__status {
        color: #949494; }
    &__desc {
        @include clr; }
    &__name, &__shipment {
        list-style: none;
        display: inline-block;
        margin: 0;
        margin-right: 50px;
        @include sm {
            display: block; } }
    h5 {}

    &__name {}

    &__list {
        @include clr;
        //padding: 15px 0;
        margin-bottom: 15px;
        > li {
            float: left;
            margin-bottom: 5px;
            width: 50%; } }
    &__label, &__value {
        margin-bottom: 5px; }

    &__label {
        float: left;
        margin-right: 10px;
        color: #949494; }
    &__value {
        padding-left: 90px;
        color: #333333; } }

.order-full {
    &__items {
        width: 100%; }
    &__item {
        border: 1px solid #BDBDBD;
        border-radius: 5px;
        list-style: none;
        margin: 0;
        margin-bottom: 1em;
        padding: 15px 15px;
        font-size: 16px;
        color: #333333;
 }        //align-items: center;
    &__item {
        @include pseudo;
        &:after {
            width: 100%;
            height: 100%;
            background: #f5f5f5;
            background: rgba(0, 0, 0, 0.03);
            z-index: -1;
            opacity: 0;
            transition: opacity .2s, width .4s; }
        &:hover::after {
            width: 100%;
            height: 100%;
            opacity: 1; } }
    &__item {
        display: flex;
        flex-grow: 1;
        padding-left: 100px;
        @include sm {
            flex-direction: column; } }
    &__item-tovar, &__item-tovarprice {
        flex-grow: 1; }

    &__item-tovar {
        display: flex;
        flex-direction: column;
        position: relative; }

    &__item-tovar .img-block {
        position: absolute;
        left: -85px;
        top: 0;
        z-index: -2; }

    &__item-tovarprice {
        display: flex;
        flex-direction: row;
        @include md {
            flex-direction: column; } }

    &__item {
        &-col {
            flex-grow: 1;
            margin: 0 1% 0 0;
 }            //min-width: 150px;
        &-title {
            font-size: 16px;
            font-weight: normal;
            text-transform: uppercase;
            color: #333333;
            max-height: 40px;
            overflow: hidden; }
        .item {
            &__list {
                color: #949494;
                font-size: 14px;
                line-height: 14px;
                display: flex;
                align-items: flex-start;
                width: 100%;
                margin-top: 10px; }
            &__title,
            &__value {} }

        .item__list.price {
            .item__list {
                margin-top: 0; }
            .item__title {
                display: none;
                @include md {
                    display: block;
                    width: 50%;
                    margin-right: 2%; } }
            .item__value {
                width: 50%;
                font-weight: bold;
                white-space: nowrap;
                color: #333333;
                @include md {
                    text-align: right; } } }
        &-col.dsc {
            @include md {
                background: #F2F2F2;
                padding: 0 10px 5px 10px;
                margin: 10px 0;
                .item__list.price {
                    display: none; } } }

        .item__list.desc {
            &__title,
            &__value {
                flex-grow: 1; }
            .item__title {
                width: 60px; }
            .item__value {
                text-align: left; } }
        .item__list.discount {
            .item__title {
                width: 80%;
                margin-right: 2%; }
            .item__value {
                width: 10%;
                text-align: right; } } }
    .total {
 }        //flex-direction: row-reverse
    .itemtotal {
        &__col {
            width: 50%;
            //margin-left: 10%
            margin-left: 0;
            &:first-child {
                margin-left: 0; }
            @include sm {
                width: initial;
                margin: 0; } }
        &__list {
            display: flex;
            align-items: flex-start;
            flex-direction: row;

            color: #949494;
            font-size: 16px;
            line-height: 16px;
            width: 100%;
            margin-bottom: 20px; }
        &__title {
            margin-right: 10px;
            //width: 120px;
            width: 60%;
            @include sm {
                text-align: right;
                width: 50%; } }
        &__value {
            color: #333333;
            @include sm {
                text-align: left;
                width: 50%; } }


        &__col.col1 {
            .itemtotal__list {
                margin-bottom: 1em;
                &:last-child .itemtotal__value {
                    font-weight: bold; } }
            .itemtotal__title {
                margin-right: 10px;
                text-align: right;
                width: 50%;
                padding-right: 5px;
                @include sm {
                    width: 50%; } }
            .itemtotal__value {
                width: 50%;
                @include sm {
                    width: initial;
                    width: 50%; }
                .btn {
                    @include md {
                        padding: 0; } } } }
        &__col {
            .black {
                color: #333333; }
            .gray {
                color: #949494; }
            .dotted {
                color: #949494;
                border-bottom: 1px dotted #949494; }
            .bold {
                font-weight: 700; } } }
    // Лояльность
    .loyalty {
        .check_card {
            display: flex;
            .form-control {
